import React, { Component } from 'react';
import './App.css';
import TreeSelect from 'react-do-tree-select'
// import TreeSelect from './component/treeSelect/index.js'
// import Data from './data'

class App extends Component {
    constructor(props) {
        super(props);
        this.state = {
            treeData: [],
            selectVal: '',
            checkbox: false, 
            parentChain: true, 
            childrenChain: true, 
            halfChain: true, 
            level: 1, 
            levelLimit: 3 ,
            updataState: false,
            disabled: 5,
            count: 0,
            _checkbox: {
                enable: false, 
                parentChain: true, 
                childrenChain: true, 
                halfChain: true, 
                initCheckedList: []
            }
        }
        this.onSelect = this.onSelect.bind(this);
        this.onChecked = this.onChecked.bind(this);
        this.customIconRender = this.customIconRender.bind(this);
        this.onDel = this.onDel.bind(this);
        this.onEdit = this.onEdit.bind(this);
        this.delTreeDataChildren = this.delTreeDataChildren.bind(this);
        this.customTitleRender = this.customTitleRender.bind(this);

        this.submit = this.submit.bind(this);
        this.onChange = this.onChange.bind(this);

    }


    componentDidMount() {
        const { level, levelLimit, disabled } = this.state;
        const _count = {
            count: 0
        }
        const treeData = this.generateData(level, levelLimit, disabled, _count)
        this.setState({
            treeData,
            count: _count.count
        })
    }

    customIconRender(item) {
        return (
            <span>
                <i onClick={(e) => { this.onEdit(item, e) }} className={`do-icon-edit`}></i>
                <i onClick={(e) => { this.onDel(item, e) }} className={`do-icon-trash`}></i>
            </span>
        )
    }

    onEdit(item, e) {
        console.log(item)
        e && e.stopPropagation();
    }

    onDel(item, e) {
        console.log(item)
        e && e.stopPropagation();

        const { treeData } = this.state;
        const _treeData = this.delTreeDataChildren(treeData, item.value)
        this.setState({
            treeData: _treeData
        })
    }

    customTitleRender(item) {
        return (
            item.title
        )
    }

    onSelect(val, e) {
        console.log(val)
    }

    onChecked(val, e) {
        console.log(val)
    }

    delTreeDataChildren(treeData, val) {
        let _treeData = treeData.concat([]);
        _treeData = _treeData.filter((item) => {
            if (item.value === val) {
                return false
            }
            if (item.children && item.children.length > 0) {
                item.children = this.delTreeDataChildren(item.children, val)
            }
            return true
        })
        return _treeData
    }

    generateData(level, levelLimit, disabled, _count, _currentLevel, _idx) {
        const list = []
        let currentLevel = _currentLevel
        if(currentLevel || currentLevel == 0) {
            currentLevel ++
        }else {
            currentLevel = 0
        }
        for (let i = 0; i < levelLimit; i++) {
            let idx = _idx || _idx == 0 ? _idx + '-' + i : i
            _count.count ++
            list.push({
                title: 'T-' + idx,
                value: idx + '',
                disabled: i > disabled,
                children: currentLevel<level ? this.generateData(level, levelLimit, disabled, _count, currentLevel, idx) : null
            })
        }
        return list
    }

    generateDataChildren(idx, len) {
        const list = []
        for (let i = 0; i < len; i++) {
            list.push({
                title: '层级-' + idx + '-' + i,
                value: idx + '-' + i,
                disabled: i < 1,
                children: this.generateDataChildren2(idx + '-' + i, 3)
            })
        }
        return list
    }

    generateDataChildren2(idx, len) {
        const list = []
        for (let i = 0; i < len; i++) {
            list.push({
                title: '层级-' + idx + '-' + i,
                value: idx + '-' + i,
                disabled: i < 1,
                children: this.generateDataChildren3(idx + '-' + i, 3)
            })
        }
        return list
    }

    generateDataChildren3(idx, len) {
        const list = []
        for (let i = 0; i < len; i++) {
            list.push({
                title: '层级-' + idx + '-' + i,
                value: idx + '-' + i,
                disabled: i < 1,
                children: this.generateDataChildren4(idx + '-' + i, 3)
            })
        }
        return list
    }

    generateDataChildren4(idx, len) {
        const list = []
        for (let i = 0; i < len; i++) {
            list.push({
                title: '层级-' + idx + '-' + i,
                value: idx + '-' + i
            })
        }
        return list
    }

    submit() {
        const { treeData, checkbox, parentChain, childrenChain, halfChain, level, levelLimit, disabled, count } = this.state;
        const _count = {
            count: 0
        }

        const _level = Number(level) + 1
        if(_level - 1 > 100 ) {
            return alert('层级不能超过100')
        }
        const _levelLimit = +levelLimit
        let preCount = _levelLimit * (1 - Math.pow(_levelLimit, _level)) / (1-_levelLimit)

        if(_levelLimit == 1) {
            preCount = (level + 1) * _levelLimit
        }
        if(_levelLimit > 1 && preCount > 1000 * 1000 ) {
            // 如果总数超过100W条
            return alert('当前的总条数为：' + preCount + ', 超过100W条，可能会影响性能，请重新选择数据参数')
        }
        const _treeData = this.generateData(level, levelLimit, disabled, _count)
        
        this.setState({
            treeData: _treeData,
            disabled: disabled,
            count: _count.count,
            _checkbox: {
                enable: checkbox,
                parentChain,
                childrenChain,
                halfChain,
                initCheckedList: []	// 初始化多选数组
            }
        })
    }

    onChange(type, e) {
        const { treeData, checkbox, parentChain, childrenChain, halfChain } = this.state;
        switch (type) {
            case 'checkbox':
                this.setState({[type]: !checkbox})
                break;
            case 'parentChain':
                this.setState({[type]: !parentChain})
                break;
            case 'childrenChain':
                this.setState({[type]: !childrenChain})
                break;
            case 'halfChain':
                this.setState({[type]: !halfChain})
                break;
            case 'level':
                this.setState({[type]: e.target.value })
                break;
            case 'levelLimit':
                this.setState({[type]: e.target.value })
                break;
            case 'disabled':
                this.setState({[type]: e.target.value })
                break;
            default: 
                break;
        }
    }

    render() {
        const { treeData, _checkbox, checkbox, parentChain, childrenChain, halfChain, level, levelLimit, disabled, count } = this.state;
        // const _checkbox = {
        //     enable: true,
        //     parentChain: true, 	// 影响父级节点； 
        //     childrenChain: true,	// 影响子级节点;		
        //     halfChain: true, 	// 子级节点被全部选中影响父级节点半选； 
        //     initCheckedList: []	// 初始化多选数组
        // }
        return (
            <div className="App">
                <div style={{ float: 'left', width: '50%', textAlign: 'left', paddingLeft: '100px', boxSizing: 'border-box' }}>
                    <div>
                        <h3>数据</h3>
                        <span>层级: <input type="number" value={level} onChange={(e)=>this.onChange('level', e)} /></span>
                        <span> 每层条数: <input type="number" value={levelLimit} onChange={(e)=>this.onChange('levelLimit', e)} /></span>
                        <span>总条数：{count}</span>
                    </div>
                    <div>
                        <h3>配置</h3>
                        <div>复选功能: <input type="checkbox" name="checkbox" id="checkbox" size="20" checked={checkbox} onChange={(e)=>this.onChange('checkbox')} />开/关 </div>
                        <div>parentChain功能: <input type="checkbox" name="parentChain" id="parentChain" size="20" checked={parentChain} onChange={(e)=>this.onChange('parentChain')} />开/关 </div>
                        <div>childrenChain功能: <input type="checkbox" name="childrenChain" id="childrenChain" size="20" checked={childrenChain} onChange={(e)=>this.onChange('childrenChain')} />开/关 </div>
                        <div>halfChain功能: <input type="checkbox" name="halfChain" id="halfChain" size="20" checked={halfChain} onChange={(e)=>this.onChange('halfChain')} />开/关 </div>
                        <div>disabled功能: 每层索引大于<input type="number" value={disabled} onChange={(e)=>this.onChange('disabled', e)} /> 禁用</div>
                    </div>
                    <div>
                        <button onClick={this.submit}>提交</button>
                    </div>
                </div>
                <div style={{ float: 'left', width: '50%' }}>
                    <TreeSelect
                        treeData={treeData}
                        style={{ width: 250, height: 500 }}
                        selectVal={this.state.selectVal}
                        onSelect={this.onSelect}
                        onChecked={this.onChecked}
                        checkbox={_checkbox}
                        showlevel={0}
                        customTitleRender={this.customTitleRender}
                    />
                </div>
            </div>
        );
    }
}

export default App;



